import { useState, useEffect } from 'react'
import { Provider } from 'react-redux'
import { store } from './store'
import WeatherCard from './components/weather-card/WeatherCard'
import CitySelector from './components/city-selector/CitySelector'
import { useLocalStorage } from './hooks/useLocalStorage'
import { CITIES } from './utils/constants'
import './App.scss'

const App: React.FC = () => {
  
  const [selectedCity, setSelectedCity] = useLocalStorage<string>('selected-city', '')
  
  //  vs 5
  const [viewMode, setViewMode] = useState<'current' | 'forecast'>('current')
  
  // 
  const [isPollingEnabled, setIsPollingEnabled] = useState(true)

  
  // 
  useEffect(() => {
    //  some()  CITIES 
    const isValidCity = CITIES.some(city => city.name === selectedCity)
    
    // 
    if (!isValidCity) {
      setSelectedCity('')
    }
  }, [selectedCity, setSelectedCity])



  
  // 
  const handleCityChange = (cityName: string) => {
    setSelectedCity(cityName)
    //  useLocalStorage
  }

  // 
  const handleViewModeChange = (mode: 'current' | 'forecast') => {
    setViewMode(mode)
  }

  const togglePolling = () => {
    setIsPollingEnabled(prev => {
      return !prev
    })
  }

  // 
  const currentCityDisplay = CITIES.find(city => city.name === selectedCity)?.displayName || selectedCity


  return (
    <Provider store={store}>
      <div className="app-container">
        
        <header className="app-header">
          <h1 className="app-title"> </h1>
          <p className="app-subtitle">
              RTK Query  TypeScript
          </p>
          
          <div className="api-status">
            <span className="status-dot"></span>
            API
          </div>
        </header>

        <main className="app-main">
          <div className="app-layout">
            
            {/*  */}
            <aside className="app-sidebar">
              
              {/*  */}
              <CitySelector
                selectedCity={selectedCity}       
                onCityChange={handleCityChange}   
                showHistory={true}                 
              />

              <div className="control-panel">
                <h4 className="control-title"></h4>
                
                <div className="view-mode-toggle">
                  <button
                    className={`toggle-button ${
                      viewMode === 'current' ? 'active' : ''
                    }`}

                    onClick={() => handleViewModeChange('current')}
                  >
                     
                  </button>
                  <button
                    className={`toggle-button ${
                      viewMode === 'forecast' ? 'active' : ''
                    }`}
                    onClick={() => handleViewModeChange('forecast')}
                  >
                     5
                  </button>
                </div>

                {/*  */}
                <div className="polling-toggle">
                  <label className="toggle-label">
                    <input
                      type="checkbox"
                      checked={isPollingEnabled}        // 
                      onChange={togglePolling}          // 
                      className="toggle-checkbox"
                    />
                    <span className="toggle-slider"></span>
                     (5)
                  </label>
                </div>

                {/*  */}
                <div className="current-info">
                  <div className="info-item">
                    <span className="info-label">:</span>
                    <span className="info-value">{currentCityDisplay}</span>
                  </div>
                  <div className="info-item">
                    <span className="info-label">:</span>
                    <span className="info-value">
                      {viewMode === 'current' ? '' : '5'}
                    </span>
                  </div>
                </div>
              </div>
            </aside>

            {/*  */}
            <section className="app-content">
              
              {/*  */}
              <WeatherCard
                cityName={selectedCity}                      // 
                showForecast={viewMode === 'forecast'}       // 
                enablePolling={isPollingEnabled}             // 
              />

              {/*  */}
              <div className="additional-info">
                <div className="info-card">
                  <h4> </h4>
                  <ul>
                    <li>RTK Query </li>
                    <li>TypeScript </li>
                    <li>Vercel API Routes </li>
                    <li></li>
                  </ul>
                </div>

                <div className="info-card">
                  <h4> </h4>
                  <p>
                    <strong></strong>
                    APICORS
                  </p>
                </div>
              </div>
            </section>
          </div>
        </main>


        <footer className="app-footer">
          <p>
             | 
            React 18 + RTK Query + TypeScript + Vercel
          </p>
          <p className="footer-note">
            
          </p>
        </footer>
      </div>
    </Provider>
  )
}

export default App