// Redux Store 
//  Redux Toolkit  store 
import { configureStore } from '@reduxjs/toolkit'
//  API
import { weatherApi } from './weatherApi'

//  Redux store
export const store = configureStore({
  reducer: {
    //  weatherApi  reducer  store 
    //  weatherApi: weatherApi.reducer
    [weatherApi.reducerPath]: weatherApi.reducer,
  },
  // middleware
  middleware: (getDefaultMiddleware) =>
    getDefaultMiddleware()                  // 
      .concat(weatherApi.middleware),       //  RTK Query 
})

//  TypeScript 
export type RootState = ReturnType<typeof store.getState>     //  state 
export type AppDispatch = typeof store.dispatch               // dispatch 