import { useWeatherData } from '../../hooks/useWeatherData'
import { getComfortLevel } from '../../utils/weatherUtils'
import { formatTime } from '../../utils/weatherUtils'
import LoadingSpinner from '../loading-spinner'
import ErrorFallback from '../error-fallback'
import ForecastItem from './ForecastItem'
import DetailItem from './DetailItem'
import styles from './WeatherCard.module.scss'

interface WeatherCardProps {
  cityName: string
  showForecast?: boolean
  enablePolling?: boolean
}


const WeatherCard: React.FC<WeatherCardProps> = ({
  cityName,
  showForecast = true,
  enablePolling = false
}) => {
  const {
    currentWeather,
    currentWeatherLoading,
    currentWeatherError,
    forecast,
    forecastLoading,
    forecastError,
    refetchWeather,
    refetchForecast,
  } = useWeatherData(cityName, {
    enablePolling,
    pollingInterval: 300000,
  })
  

  //
  if (showForecast ? forecastLoading : currentWeatherLoading) {
    return (
      <div className={styles.card}>
        <LoadingSpinner 
          message={`${showForecast ? '' : ''}...`}
          size="medium"
        />
      </div>
    )
  }

  // 
  if (showForecast ? forecastError : currentWeatherError) {
    return (
      <div className={styles.card}>
        <ErrorFallback 
          error={showForecast ? forecastError : currentWeatherError}
          onRetry={showForecast ? refetchForecast : refetchWeather}
          cityName={cityName}
        />
      </div>
    )
  }

  //  
  if (showForecast && forecast) {
    return (
      <div className={styles.card}>
        <div className={styles.header}>
        <h2 className={styles.cityName}>{forecast?.cityName} - 5</h2>
        </div>
        <div className={styles.forecastGrid}>

          { forecast?.forecast.map((day, index) => (
              <ForecastItem 
                key={day.date}
                {...day}
                isToday={index === 0}
              />
          ))}

        </div>
      </div>
    )
  }


  // 
  if (currentWeather) {
    //  currentWeather 
    const comfortLevel = getComfortLevel(currentWeather.temperature, currentWeather.humidity)
    
    //  DetailItem 
    const weatherInfo = [
      { label: '', value: comfortLevel },
      { label: '', value: currentWeather.humidity, unit: '%' },
      { label: '', value: currentWeather.windSpeed, unit: ' m/s' },
      { label: '', value: currentWeather.pressure, unit: ' hPa' }
    ]
    
    return (
      <div className={styles.card}>
        <div className={styles.header}>
          <h2 className={styles.cityName}>{currentWeather.cityName}</h2>
          <div className={styles.lastUpdated}>
            : {formatTime(currentWeather.lastUpdated)}
          </div>
          <div className={styles.description}>
            {currentWeather.description}
          </div>
        </div>

        <div className={styles.weatherDetails}>

          {weatherInfo.map((item, idx) => (
            <DetailItem
              key={idx}
              label={item.label}
              value={item.value}
              unit={item.unit}
            />
          ))}

        </div>

        <button className={styles.refreshButton} onClick={refetchWeather}>
           
        </button>

      </div>
    )
  
  }

  return (
    <div className={styles.card}>
      <div className={styles.noData}></div>
    </div>
  )

}

export default WeatherCard